.book-content {
  display: flex;
  gap: 60px;
  margin-top: 3%;
}

.book-info-aside {
  width: 50%;
  display: flex;
  gap: 20px;
  align-items: flex-start;
}

.book-image {
  flex-basis: 0;
  width: 50%;
  float: left;
  margin-right: 10px;
}

.book-details {
  /* flex-basis: 0; */
  flex-grow: 1;
  overflow: hidden;
}

.book-title {
}

.book-description {
}

.book-author {
}

.puzzle-grid {
  display: grid;
  grid-template-columns: repeat(9, 50px);
  /* grid-template-rows: repeat(3, 30px); */
  width: 1%;
  grid-gap: 5px 10px;
}

.puzzle-grid-element {
  box-sizing: border-box;
  /* display: inline-block; */
  color: black;
  text-align: center;
  width: 100%;
  /* padding: 5px 15px 15px 5px; */
  /* margin-right: 20px; */
  /* margin-top: 15px !important; */
  /* margin-bottom: 15px !important; */
  /* border-radius: 5px; */
  box-shadow: inset 0 4px 7px 1px #ffffff,
    inset 0 -5px 20px rgb(173 186 204 / 25%), 0 2px 6px rgb(0 21 64 / 14%),
    0 10px 20px rgb(0 21 64 / 5%);
  background: linear-gradient(#f5f5f5, #ffffff);
  transition: 0.4s all;
  text-decoration: none;
}

.puzzle-grid-element:hover {
  box-shadow: inset 0 4px 7px 1px #ffffff,
    inset 0 -5px 20px rgb(173 186 204 / 25%), 0 2px 12px rgb(0 21 64 / 14%),
    0 10px 20px rgb(0 21 64 / 30%);
}
