.baduk-board {
  position: relative;
  background-image: url("./images/board.png");
}

.board-grid {
  filter: drop-shadow(rgba(23, 10, 2, 0.4) 0px 5.65294px 11.3059px);
  display: inline-grid;
}

.board-row {
  flex-wrap:nowrap;
  display: flex;
}

.intersection {
  flex-shrink: 0;
  touch-action: none;
  background-size: 92%;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  z-index: 1;
  vertical-align: top;
  padding: 0;
  margin: 0; 
}

.intersection[data-color="B"] {
  background-image: url("./images/black.png");
}

.intersection[data-color="W"] {
  background-image: url("./images/white.png");
}

svg {
  position: absolute;
  display: block;
  z-index: 0;
  width: 100%;
  height: 100%;
  overflow: visible
}