.book-showcase {
  display: inline-block;
  width: 280px;
  margin-right: 20px;
  margin-top: 15px !important;
  margin-bottom: 15px !important;
  border-radius: 15px;
  box-shadow: inset 0 4px 7px 1px #ffffff,
    inset 0 -5px 20px rgb(173 186 204 / 25%), 0 2px 6px rgb(0 21 64 / 14%),
    0 10px 20px rgb(0 21 64 / 5%);
  background: linear-gradient(#f5f5f5, #ffffff);
  transition: 0.4s all;
}

.book-showcase:hover {
  box-shadow: inset 0 4px 7px 1px #ffffff,
    inset 0 -5px 20px rgb(173 186 204 / 25%), 0 2px 12px rgb(0 21 64 / 14%),
    0 10px 20px rgb(0 21 64 / 30%);
}

.book-showcase-upper {
  height: 200px;
  background-color: #fbab7e;
  background-image: linear-gradient(62deg, #fbab7e 0%, #f7ce68 100%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  /* background-blend-mode: darken; */
  /* filter: brightness(80%); */

  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  padding: 15px;
}

.book-showcase-title {
  text-shadow: 0 0 20px rgb(0 0 0 / 66%);
  line-height: 1.2;
  margin-top: 7px;
  font-size: 30px;
  font-weight: 600;
  color: white;
}

.book-showcase-author {
  text-shadow: 0 0 20px rgb(0 0 0 / 60%);
  color: white;
  font-weight: 500;
  line-height: 18px;
  font-size: 16px;
  opacity: 0.8;
}

.book-showcase-lower {
  height: 84px;
  padding: 10px;
  text-align: center;
  line-height: 1.7;
  width: calc(100% - 55px);
}

.book-showcase-info {
  width: 50%;
  float: left;
}

.book-showcase-info-number {
  font-size: 26px;
  font-weight: 500;
  color: #222222;
  margin: 0;
}

.book-showcase-info-text {
  margin-top: -5px;
  font-size: 12px;
  color: grey;
}
